#sidebar {
  max-width: 240px;
  min-width: 240px;
  height: 100vh;
  transition: all 0.4s ease;
}

#wrapper {
  max-width: 240px;
  min-width: 240px;
  height: 100vh;
  transition: all 0.4s ease;
}

.toggle {
  margin-left: -240px;
}

.focus-none:focus {
  outline: none;
  box-shadow: none;
}

.focus-none:active {
  outline: none;
  box-shadow: none;
}

.opacity {
  opacity: 0.3;
}

.opacity-1 {
  background: #00000070;
}

.text-blue {
  color: #1c82f5 !important;
}

.bg-blue {
  background: linear-gradient(90deg, #71b7f8 0%, #67a1f7 49%, #6787f7 100%);
}

.row-4 {
  max-height: 510px;
  min-height: 510px;
}

.row-3 {
  max-height: 330px;
  min-height: 330px;
}

.p-35 {
  padding: 30%;
}

.bg-about {
  background-image: url("../image/bg_about.jpg");
  background-size: 100%;
  background-repeat: no-repeat;
}

.hr-custom {
  border : 0.4px solid white !important;
}

.w-img {
  width : 100px;
  height : 100px;
}

.m-nt {
  margin-top : -50px;
}

.btn-blue {
  background: linear-gradient(90deg, #71b7f8 0%, #67a1f7 49%, #6787f7 100%);
}

.w-btn {
  width: 100px;
  height: 100px;
}

.text-btn {
  font-size: x-small;
}

.icons-size {
  font-size: 100px;
  font-weight: bold;
}